@import url(https://necolas.github.io/normalize.css/8.0.1/normalize.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  src: local('Merriweather Regular'), local('Merriweather-Regular'), url(https://fonts.gstatic.com/s/merriweather/v19/u-440qyriQwlOrhSvowK_l5-fCZJ.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  src: local('Open Sans Regular'), local('OpenSans-Regular'), url(https://fonts.gstatic.com/s/opensans/v15/mem8YaGs126MiZpBA-UFVZ0e.ttf) format('truetype');
}
@font-face {
  font-family: 'Roboto Mono';
  font-style: normal;
  font-weight: 400;
  src: local('Roboto Mono'), local('RobotoMono-Regular'), url(https://fonts.gstatic.com/s/robotomono/v5/L0x5DF4xlVMF-BfR8bXMIjhLq3o.ttf) format('truetype');
}
body {
  background-color: #444444;
  font-family: 'Open Sans', sans-serif;
}
body .App {
  width: 80vw;
  margin: auto;
  padding: 15px;
  background-color: #f1f1f1;
  box-shadow: 0 0 5px 5px #2e2e2e;
}
body .App .header {
  font-size: 1.5em;
  font-family: 'Merriweather', serif;
}
body .App .row {
  padding: 1em;
  margin: 0.5em;
  border: 0.7px solid #dddddd;
  border-radius: 0.5em;
}
body .App .row:hover {
  background-color: #e9e9e9;
}
body .App .row .title {
  font-size: 1.2em;
  font-family: 'Merriweather', serif;
}
body .App .row .subtitle {
  font-size: 0.95em;
  font-style: oblique;
  color: #303030;
}
body .App .row .description {
  margin: 0.5em 0 0.5em 0;
}
body .App .row .pair {
  font-size: 0.95em;
  display: flex;
  justify-content: space-between;
  padding-left: 2em;
}
body .App .row .pair .period-container {
  color: white;
  background-color: #303030;
  border: 3px solid black;
  border-radius: 5px;
}
body .App .row .pair .period-container .period {
  font-size: 0.9em;
  font-family: 'Roboto Mono', monospace;
}

body .App .profile-container .row {
  display: flex;
}
body .App .profile-container .row .profile-image-container {
  padding-right: 1.5em;
}
body .App .profile-container .row .profile-image-container img {
  min-height: 115px;
  min-width: 115px;
  height: 115px;
  width: 115px;
  border-radius: 50%;
}
body .App .profile-container .row .profile-details-container {
  width: 60vw;
}
body .App .other-container .pair {
  display: flex;
}
body .App .other-container .pair .other-title {
  min-width: 8em;
}
body .App .other-container .pair .other-description {
  flex-grow: 1;
  margin: 0;
}
body .App .footer-container {
  border: 0;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}
body .App .footer-container:hover {
  background-color: #f1f1f1;
}
body .App .footer-container img {
  padding: 1em;
  height: 3em;
}

