body .App .profile-container .row {
  display: flex;
}
body .App .profile-container .row .profile-image-container {
  padding-right: 1.5em;
}
body .App .profile-container .row .profile-image-container img {
  min-height: 115px;
  min-width: 115px;
  height: 115px;
  width: 115px;
  border-radius: 50%;
}
body .App .profile-container .row .profile-details-container {
  width: 60vw;
}
body .App .other-container .pair {
  display: flex;
}
body .App .other-container .pair .other-title {
  min-width: 8em;
}
body .App .other-container .pair .other-description {
  flex-grow: 1;
  margin: 0;
}
body .App .footer-container {
  border: 0;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}
body .App .footer-container:hover {
  background-color: #f1f1f1;
}
body .App .footer-container img {
  padding: 1em;
  height: 3em;
}
